@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: gainsboro;
}
/* .App {
  text-align: center;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (min-width: 900px) {
  .product-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px auto;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 3.125rem;
  }
  .product-container > div {
    flex: 1 1;
  }
}
.product-container {
  margin: 20px auto;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 3.125rem;
  background: white;
}
img {
  width: 70%;
}
.qt {
  font-size: 3.125rem;
}
.product-control {
  display: flex;
  justify-content: space-between;
  /* border: 2px solid red; */
}

.product-details {
  text-align: left;
}
.add-to-cart-button {
  width: 30%;
  margin: auto;
}
.qt-text {
  text-align: center;
}


.nav-container {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-right {
  display: flex;
  margin-right: 1.25rem;
}
.navbar-right a {
  color: aliceblue;
}
li {
  list-style: none;
  margin-right: 20px;
}
li i,
.cart-brand {
  color: black;
}

.badge {
  background: blue;
  color: aliceblue;
}
.brand {
  margin-left: 20px;
  font-size: 1.875rem;
}



.lighter-text {
  color: #abb0be;
}

.main-color-text {
  color: #6394f8;
}

.cart-container {
  background: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.badge {
  background-color: #6394f8;
  border-radius: 10px;
  color: white;
  display: inline-block;
  font-size: 12px;
  line-height: 1;
  padding: 3px 7px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}

.shopping-cart {
  margin: 20px 0;
  background: white;
  width: 320px;
  position: absolute;
  border-radius: 3px;
  padding: 20px;
  z-index: 1;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.shopping-cart .shopping-cart-header {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 15px;
}

.shopping-cart .shopping-cart-items {
  padding: 20px 0;
}

.shopping-cart .shopping-cart-items li {
  margin-bottom: 18px;
}

.shopping-cart .shopping-cart-items img {
  width: 40%;
  object-fit: contain;
}

.shopping-cart .shopping-cart-items .item-name {
  display: block;
  padding-top: 10px;
  font-size: 16px;
}

.shopping-cart .shopping-cart-items .item-price {
  color: #6394f8;
  margin-right: 8px;
}

.shopping-cart .shopping-cart-items .item-quantity {
  color: #abb0be;
}

.shopping-cart:after {
  bottom: 100%;
  left: 89%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: white;
  border-width: 8px;
  margin-left: -8px;
}

.cart-icon {
  color: #515783;
  font-size: 24px;
  margin-right: 7px;
  float: left;
}

.button {
  background: #6394f8;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  display: block;
  border-radius: 3px;
  font-size: 16px;
  margin: 25px 0 15px 0;
}

.cart-control {
  display: flex;
  justify-content: flex-end;
  margin-right: 30px;
}

.display-checkout {
  display: none;
}

.shopping-cart-header {
  display: flex;
  justify-content: space-between;
}

.shopping-cart-item {
  display: flex;
  margin-bottom: 20px;
}

.shopping-cart-item-image {
  padding-right: 10px;
}

ul .shopping-cart-items {
  padding: 0;
}

.shopping-cart-item-details-empty {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
/*# sourceMappingURL=Cart.css.map */
.pagination-container {
  display: flex;
  justify-content: center;
}

li {
  margin: 0;
}
